body{
  margin: 0px;
  padding: 0px;
}
.App{
  text-align: center;
  margin: 50px auto 0px;
  width: 90%;
  max-width: 700px;
  border-radius: 20px;
  height: 470px;
  padding: 20px 15px;
  background-color: rgb(0, 255, 166);
}
.search-engine{
  width: 100%;
  display: flex;
  justify-content:space-around;
  align-items: center;
  margin-bottom: 30px;
  margin-top: 10px;

}
.search-engine input{
  width: 70%;
  height: 45px; 
  border: 1px solid;
  border-radius: 8px;
  font-size: 16px;
  outline:none ;
  background-color: white;
  color: #555555;
}
.search-engine button{
  border: none;
  border-radius: 8px;
  background-color: black;
  color: white;
  font-size: 20px;
  outline: none;
  cursor: pointer;
  padding: 13px 15px;
}
.city-name{
  margin-bottom: 10px;
  
}
.loading{
  font-size: 60px;
  font-weight: bolder;
  color: #000;
}
.date{
  font-size:18px ;
  font-weight: 500;
  font-style: italic;
}
.temp{
  font-size: 64px;
  color: black;
  font-weight: bold;
  text-align: center;
}
.description{
  color: black;
  font-size: 22px;
  font-weight: 500;
  margin-top: 0px;
  margin-bottom: 20px;
}
.weather-info{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 20px;
  padding: 0px 20px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}
.column{
  display: flex;
  align-items: center;
  line-height: 1px;
}